.document-update-header {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.document-update-form {
    .field {
        margin-bottom: var(--sp-2);

        label {
            display: block;
            padding-bottom: var(--sp-1);
        }
    }
}

.document-update-view {
    .document-update-header {
        input {
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
            border: 1px solid transparent;
        }
    }
}

.document-update-view-actions {
    display: flex;
    align-items: center;

    .btn {
        --icon-size: 20px;

        color: var(--gray-600, black);
        border-color: transparent;

        &:hover {
            color: var(--gray-800, black);
        }
    }
}
